import { createAsyncThunk } from "@reduxjs/toolkit";
import {
  doAddTask,
  doDeleteTask,
  doFetchTasks,
  doMarkAsDone,
} from "./todo-data-access";

export const fetchTasks = createAsyncThunk("tasks/fetch", doFetchTasks);

export const markTaskAsDone = createAsyncThunk(
  "tasks/markAsDone",
  doMarkAsDone(true)
);

export const markTaskAsOpen = createAsyncThunk(
  "tasks/markAsOpen",
  doMarkAsDone(false)
);

export const createTask = createAsyncThunk("tasks/create", doAddTask);

export const deleteTask = createAsyncThunk("tasks/delete", doDeleteTask);
