import React, { useState } from "react";
import styles from "./quote.module.css";
import reroll from "./reroll.svg";

const quotes = [
  <span key="birthday">Happy Birthday! 🥳🎂</span>,
  <em key="herQuote">
    &quot;Isn&apos;t it crazy that this is literally another rock floating
    around in space which is attracted to the earth by the law of gravity and we
    can only see it because it gets lit up by the sun?&quot;
  </em>,
  <a
    key="rickRoll"
    className={styles.link}
    href="https://youtu.be/dQw4w9WgXcQ"
    rel="noreferrer"
    target="_blank"
  >
    https://youtu.be/dQw4w9WgXcQ
  </a>,
  <span key="wwww">🥶WWWWWWWWWWWWWWWWWW🥶</span>,
];

const randomQuote = () => quotes[Math.floor(Math.random() * quotes.length)];

export const Quote = () => {
  const [quote, setQuote] = useState(quotes[0]);
  return (
    <div className={styles.container}>
      <p className={styles.quote}>{quote}</p>
      <img
        className={styles.reroll}
        src={reroll}
        onClick={() => setQuote(randomQuote())}
      ></img>
    </div>
  );
};
