import { createSlice } from "@reduxjs/toolkit";
import { TodoState } from "./state";
import {
  createTask,
  deleteTask,
  fetchTasks,
  markTaskAsDone,
  markTaskAsOpen,
} from "./todo.actions";

export const todoSlice = createSlice({
  name: "todo",
  initialState: { tasks: [] } as TodoState,
  reducers: {},
  extraReducers: (builder) => {
    builder.addCase(fetchTasks.fulfilled, (state, action) => {
      state.tasks = action.payload;
    });
    builder.addCase(createTask.pending, (state) => {
      state.tasks.push(undefined);
    });
    builder.addCase(createTask.fulfilled, (state, action) => {
      state.tasks[state.tasks.findIndex((t) => t === undefined)] =
        action.payload;
    });
    builder.addCase(markTaskAsDone.pending, (state, action) => {
      const newlyMarkedTask = state.tasks.find(
        (t) => t?.id === action.meta.arg.id
      );
      if (newlyMarkedTask) {
        newlyMarkedTask.done = undefined;
      }
    });
    builder.addCase(markTaskAsDone.fulfilled, (state, action) => {
      const newlyMarkedTask = state.tasks.find(
        (t) => t?.id === action.meta.arg.id
      );
      if (newlyMarkedTask) {
        newlyMarkedTask.done = true;
      }
    });
    builder.addCase(markTaskAsOpen.pending, (state, action) => {
      const newlyMarkedTask = state.tasks.find(
        (t) => t?.id === action.meta.arg.id
      );
      if (newlyMarkedTask) {
        newlyMarkedTask.done = undefined;
      }
    });
    builder.addCase(markTaskAsOpen.fulfilled, (state, action) => {
      const newlyMarkedTask = state.tasks.find(
        (t) => t?.id === action.meta.arg.id
      );
      if (newlyMarkedTask) {
        newlyMarkedTask.done = false;
      }
    });
    builder.addCase(deleteTask.pending, (state, action) => {
      state.tasks.splice(
        state.tasks.findIndex((t) => t?.id === action.meta.arg.id),
        1
      );
    });
  },
});
