import { initializeApp } from "firebase/app";
import {
  collection,
  deleteDoc,
  doc,
  getDocs,
  getFirestore,
  query,
  setDoc,
} from "firebase/firestore";
import { Task } from "./task";

const firebaseConfig = {
  apiKey: "AIzaSyAzNr94ML71SIoHWLlD3v1hx8P51HNnHzE",

  authDomain: "moon-todo-c152b.firebaseapp.com",

  projectId: "moon-todo-c152b",

  storageBucket: "moon-todo-c152b.appspot.com",

  messagingSenderId: "887761987348",

  appId: "1:887761987348:web:5bddb86b8bc25e76fdc401",
};

const app = initializeApp(firebaseConfig);

const db = getFirestore(app);

const tasksRef = collection(db, "tasks");

export const doAddTask = async (description: string) => {
  const docRef = doc(tasksRef);
  await setDoc(docRef, {
    description: description,
    done: false,
  });
  return {
    id: docRef.id,
    description,
    done: false,
  } as Task;
};

export const doDeleteTask = async (task: Task) => {
  const docRef = doc(tasksRef, task.id);
  await deleteDoc(docRef);
};

export const doMarkAsDone = (done: boolean) => async (task: Task) => {
  await setDoc(doc(tasksRef, task.id), {
    description: task.description,
    done: done,
  });
  return {
    ...task,
    done: done,
  } as Task;
};

export const doFetchTasks = async () => {
  const q = query(tasksRef);
  const docsSnapshot = await getDocs(q);
  const tasks: Task[] = [];
  docsSnapshot.forEach((docSnapshot) =>
    tasks.push({
      id: docSnapshot.id,
      description: docSnapshot.data().description,
      done: docSnapshot.data().done,
    })
  );
  return tasks;
};
