import React, { useState } from "react";
import styles from "./moon.module.css";
import moon from "./moon.svg";
import explosion from "./explosion.svg";

type MoonState = "normal" | "shake" | "fall" | "explode" | "spawn";

const getMoonAnim = (state: MoonState): string => {
  switch (state) {
    case "shake":
      return styles.moonShake;
    case "fall":
      return styles.moonFall;
    case "explode":
      return styles.explosion;
    case "spawn":
      return styles.spawnMoon;
  }
  return "";
};

const getNextState = (e: React.AnimationEvent<HTMLDivElement>) => {
  switch (e.animationName) {
    case styles.animFall:
      return "explode";
    case styles.animExplosion:
      return "spawn";
  }
  return "normal";
};

export const Moon = () => {
  const [clicks, setClicks] = useState(0);
  const [moonState, setMoonState] = useState<MoonState>("normal");

  const moonImg = moonState === "explode" ? explosion : moon;
  const moonAnim = getMoonAnim(moonState);

  return (
    <div
      className={moonAnim}
      onAnimationEnd={(e) => setMoonState(getNextState(e))}
    >
      <img
        src={moonImg}
        alt="Moon"
        className={styles.moon}
        onClick={() => {
          if (clicks > 5) {
            setMoonState("fall");
            setClicks(0);
          } else if (moonState === "normal") {
            setMoonState("shake");
            setClicks(clicks + 1);
          }
        }}
      ></img>
    </div>
  );
};
