import React, { useEffect, useRef, useState } from "react";
import styles from "./stars.module.css";
import star from "./star.svg";
import { heartStarSign } from "./starsigns";

export interface Star {
  name: string;
  x: number;
  y: number;
  animationDuration: string;
}

const randInt = (max: number) => {
  return Math.floor(Math.random() * max);
};

const StarImg = (props: { star: Star }) => {
  const style = {
    left: props.star.x,
    top: props.star.y,
    animationDuration: props.star.animationDuration,
  };
  return <img className={styles.star} style={style} src={star}></img>;
};

const getStarElements = (stars: Star[]): JSX.Element[] => {
  return stars.map((star) => {
    return <StarImg key={star.name} star={star}></StarImg>;
  });
};

export const Stars = () => {
  const divRef = useRef<HTMLDivElement>(null);
  const [stars, setStars] = useState<Star[]>([]);

  useEffect(() => {
    const width = divRef.current?.offsetWidth;
    const height = divRef.current?.offsetHeight;
    if (width === undefined || height === undefined) {
      return;
    }
    const calcStars: Star[] = [];
    for (let i = 1; i < 50; i++) {
      calcStars.push({
        name: `${i}`,
        x: randInt(width),
        y: randInt(height),
        animationDuration: `${1500 + randInt(6500)}ms`,
      });
    }
    setStars(calcStars);
  }, [divRef.current]);

  return (
    <div className={styles.sky} ref={divRef}>
      {getStarElements(stars)}
      <div
        onClick={() =>
          setStars(
            stars.concat(
              heartStarSign.stars.map((star) => ({
                ...star,
                x: star.x + 140,
                y: star.y + 50,
              }))
            )
          )
        }
      >
        <div className={styles.clickableStar}>
          <StarImg
            star={{
              name: "heartbeat",
              x: 200,
              y: 100,
              animationDuration: "20s",
            }}
          ></StarImg>
        </div>
      </div>
    </div>
  );
};
