import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { selectTasks } from "../../data/todo.selectors";
import styles from "./todo.module.css";
import imgDone from "./done.svg";
import imgOpen from "./open.svg";
import imgDoneTransition from "./transition.svg";
import collapseBtnImg from "./collapse-button.svg";
import {
  createTask,
  deleteTask,
  markTaskAsDone,
  markTaskAsOpen,
} from "../../data/todo.actions";
import { Task } from "../../data/task";

const getIconForDone = (done: boolean | undefined) => {
  switch (done) {
    case true:
      return <img src={imgDone} className={styles.indicator}></img>;
    case false:
      return <img src={imgOpen} className={styles.indicator}></img>;
    case undefined:
      return (
        <img
          src={imgDoneTransition}
          className={`${styles.indicator} ${styles.indicatorSpinning}`}
        ></img>
      );
  }
};

const createTaskElement = (
  t: { id: string; description: string; done: boolean | undefined } | undefined,
  onClick: () => void,
  onClickDelete: () => void
) => {
  const className = `${styles.item} ${
    t?.done ? styles.itemDone : styles.itemOpen
  }`;
  return (
    <div className={styles.singleItemContainer}>
      <div key={t?.id} className={className} onClick={onClick}>
        {getIconForDone(t?.done)}
        {t?.description}
      </div>
      <div className={styles.deleteTaskBtn} onClick={onClickDelete}>
        ❌
      </div>
    </div>
  );
};

export const TodoList = () => {
  const [modalVisible, setModalVisible] = useState(false);
  const [newTaskInput, setNewTaskInput] = useState("");
  const tasks = useSelector(selectTasks);
  const dispatch = useDispatch();

  return (
    <div className={styles.container}>
      <img
        src={collapseBtnImg}
        className={styles.collapseBtn}
        onClick={() => setModalVisible(true)}
      ></img>
      <div
        className={`${styles.modal} ${
          !modalVisible ? styles.modalInvisible : ""
        }`}
      >
        <div className={styles.header}>
          <h1>TODO&apos;s</h1>
          <div
            className={styles.closeModal}
            onClick={() => setModalVisible(false)}
          >
            ✖
          </div>
          <div className={styles.taskInput}>
            <input
              type="text"
              onChange={(e) => setNewTaskInput(e.target.value)}
            ></input>
            <div
              className={styles.taskInputAddButton}
              onClick={() => dispatch(createTask(newTaskInput))}
            >
              ➕
            </div>
          </div>
        </div>
        <div className={styles.taskListContainer}>
          {tasks.map((task) =>
            createTaskElement(
              task,
              () =>
                dispatch(
                  task?.done
                    ? markTaskAsOpen(task as Task)
                    : markTaskAsDone(task as Task)
                ),
              () => {
                dispatch(deleteTask(task as Task));
              }
            )
          )}
        </div>
      </div>
    </div>
  );
};
