import React, { useState } from "react";
import styles from "./moon-image.module.css";
import imgGranattor from "./images/granattor.jpg";
import imgRadl from "./images/radl.jpg";
import collapseBtn from "./collapse-button.svg";

const images = [imgGranattor, imgRadl];

const getRandomImage = () => {
  return images[Math.floor(Math.random() * images.length)];
};

export const MoonImage = () => {
  const [imgSrc, setImgSrc] = useState<string | undefined>(undefined);
  return !imgSrc ? (
    <div className={styles.invisible}>
      <img
        className={styles.collapseButton}
        src={collapseBtn}
        onClick={() => setImgSrc(getRandomImage())}
      ></img>
    </div>
  ) : (
    <div className={styles.container} onClick={() => setImgSrc(undefined)}>
      <div
        style={{
          backgroundImage: `url("${imgSrc}")`,
        }}
        className={styles.background}
      ></div>
      <img src={imgSrc} className={styles.image}></img>
    </div>
  );
};
